* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-align: center;
    color: black
}

.tBodyModal {
    position: absolute;
    margin-left: auto;
    left: 10%;
    top: 10%;
    height: 40%;
    width: 80%;
    margin-right: auto;
}

.buttonBackModal {
    color: black;
    font-size: 22px;
    position: absolute;
    left: 20px;
    bottom: 5px;
    background-color: #9ce059;

}

.logos {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: stretch;
    justify-content: center;
    align-items: stretch;
    justify-items: center;
}

.logo {
    width: 40%;
}

.ButtonActivPageNotMain {
    display: none;
}

a {
    color: blue;
}

.trModule {
    color: black;
}


.contener {
    margin-top: 25px;
    display: grid;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: stretch;
    grid-template-columns: repeat(5, 154px);
    grid-column: rever;
    justify-items: center;
    justify-content: center;
    align-content: stretch;

}

tbody td {
    height: 100px;
    width: 25%;
    background-color: whitesmoke;
    font-size: 21px;
    margin: 20px;
    color: #020202;
    border: 2px solid whitesmoke;

}

td {
    font-size: 1.4rem;
    color: #FFFDE7;
}

.modalTable {
    height: 40%;
    display: none;
    background-color: #888b8b;
    text-align: center;
    position: fixed;
    left: 15%;
    z-index: 111;
    opacity: .8;
    width: 70%;
    border-radius: 15px;
    top: 15%;

}



.trModule {
    font-size: 34px;
}



@media only screen and (max-width: 750px) {
    .contener {
        margin-top: 25px;
        display: grid;
        flex-wrap: nowrap;
        flex-direction: column;
        align-items: stretch;
        grid-template-columns: repeat(4, 25%);
        grid-column: rever;
        justify-items: center;
        justify-content: center;
        align-content: stretch;
    }

    .buttonBackModal {
        font-size: 22px;
        color: black;
        position: absolute;
        left: 35%;
        /* height: 18%; */
        width: 30%;
        bottom: 5px;
        background-color: #9ce059;

    }
}

@media only screen and (max-width: 654px) {


    .modalTable {
        height: 43%;
        display: none;
        background-color: #979f9a;
        text-align: center;
        position: fixed;
        left: 2%;
        z-index: 111;
        opacity: .98;
        width: 96%;
        border-radius: 15px;
        top: 15%;
    }


    .tBodyModal td {
        position: relative;
        top: 16px;
        height: 90px;
        /* width: 36%; */
        background-color: whitesmoke;
        font-size: 14px;
        /* margin: 20px; */
        color: #020202;
        border: 2px solid whitesmoke;
    }

    .modalContener {
        height: 40%;
        display: none;
        background-color: #888b8b;
        text-align: center;
        position: fixed;
        left: 15%;
        z-index: 111;
        opacity: .8;
        width: 70%;
        border-radius: 15px;
        top: 15%;
    }


}

@media only screen and (max-width: 509px) {
    .tBodyModal {
        position: absolute;
        margin-left: auto;
        left: 1%;
        right: 1%;
        top: 10%;
        height: 40%;
        width: 98%;
        margin-right: auto;
    }

    .tBodyModal td {
        position: relative;
        top: 16px;
        height: 90px;
        /* width: 36%; */
        background-color: whitesmoke;
        font-size: 9.5px;
        /* margin: 20px; */
        color: #020202;
        border: 2px solid whitesmoke;
    }

    .btnClient {
        margin: 7px;
        width: 20rem;
        height: 2rem;
        font-family: revert;
        font-size: 13px;
        border-radius: 5px;
    }

    td {
        font-size: 10px;
    }

    .logo {
        max-width: 89%;
    }

    .contener {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }


}


@media only screen and (max-width: 509px) {
    .tBodyModal td {
        position: relative;
        top: 16px;
        height: 90px;
        width: 36%;
        background-color: #f3f3f3;
        font-size: 13.5px;
        /* margin: 20px; */
        color: #020202;
        border: 2px solid #201c1c;

    }
}