* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

iframe {
    width: 100%;
    height: 1000px;
}

.menuMain {
    display: grid;
    background-color: green;
    grid-template-columns: repeat(4, 24.5%);
    margin-bottom: 25px;
    padding: 0px 5px;

}

button {
    width: 8rem;
    height: 2rem;
    font-family: revert;
    font-size: 13px;
    border-radius: 5px;
    margin-top: 7px;
}

.button {
    margin: 9px;
    width: 9rem;
    font-family: cursive;
    font-size: 15px;
    border-radius: 5px;
}

.button::after {
    background-color: rgb(17, 108, 236);
}

.button:hover {
    background-color: rgb(179, 239, 95);
}




@media only screen and (max-width: 642px) {
    img {
        height: 33px;
    }

    .menuMain {
        display: grid;
        background-color: green;
        grid-template-columns: 1fr;
        margin-bottom: 25px;
        grid-template-rows: repeat(4, 1fr);
    }

    button {

        height: 2rem;
        font-family: Roboto;
        font-size: 13px;
        border-radius: 5px;
        margin-top: 7px;
    }

    .button {
        margin: 7px;
        width: 20rem;
        height: 2rem;
        font-family: Roboto;
        font-size: 13px;
        border-radius: 5px;
    }
}