.inputCalculator {
    width: 8rem;
    height: 2rem;
    font-family: Roboto;
    font-size: 13px;
    border-radius: 5px;
    margin-top: 7px;
}

@media only screen and (max-width: 500px) {
    label {
        font-size: 33px;
        font-family: Roboto;
    }

    button,
    .inputCalculator {
        font-size: 33px;
        width: 5.5rem;
        height: 3.5rem;
        font-family: Roboto;
    }
}